@import "../libs/include-media/dist/include-media";
@import "utils/special-version-variables";
@import "utils/functions";
@import "utils/mixins";
@import "utils/placeholders";

// variables
$displace: 1px;
// variables end

// только для текуще проекта
.footer-nav {
	position: relative; }

.spec-panel {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	//width: 70px
	z-index: 999;
	font-size: 16px;
	line-height: 1;
	overflow: hidden;
	box-shadow: 0 0 0 1px currentColor;
	//background-color: var(--main-bg-color);
	background-color: inherit;
	// transition
	$prop: transform;
	will-change: $prop;
	transition: {
		property: $prop;
		duration: 0.2s;
		timing-function: ease-in-out; }

	@include media('<mob-md') {
		height: 70px;
		width: 100%;
		border-width: 0 0 2px 0;
		$sh1: 0 0 0 2px;
		$sh2: 0 4px 4px;
		box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color, 0.3);

		.#{$spec-mod--schemecolor-wb} & {
			box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color--scheme-wb, 0.3); }

		.#{$spec-mod--schemecolor-blue} & {
			box-shadow: $sh1 currentColor, $sh2 rgba($spec-text-color--scheme-blue, 0.3); } }

	.vspec & {
		display: block; }

	&:hover,
	.touchevents & {
		overflow-y: auto;

		@include media('<mob-md') {
			overflow-y: hidden;
			overflow-x: auto; } }

	.nav-opened-before & {
		@include media('<mob-md') {
			transform: translate(0, -110%); } }

	.sv-btn-group {
		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top; }

		//a:last-child
		//argin-bottom: 0

		//include media('<mob-md')
 }		//margin-right: 0

	.sv-btn-sep {
		border: 0 solid currentColor;
		border-bottom-width: 1px;
		margin: 20px 0;
		font-size: 0;
		line-height: 0;

		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top;
			border-left: 1px solid currentColor;
			border: none;
			width: 0;
			height: 36px;
			margin: 0 10px 0 0; } }

	a {
		display: block;
		margin-bottom: 18px;
		margin-left: auto;
		margin-right: auto;

		@include media('<mob-md') {
			display: inline-block;
			vertical-align: top;
			margin-bottom: 0;
			margin-right: 10px; } } }

.spec-panel_h {
	padding: 20px 0;
	text-align: center;
	letter-spacing: -4px;
	font-size: 0;
	line-height: 1;

	@include media('<mob-md') {
		white-space: nowrap;
		padding: 10px; } }

.spec-panel,
.spec-panel_h {
	width: nth($spec-panel-size, 2);
	box-sizing: border-box;

	@include media('<mob-md') {
		height: nth($spec-panel-size--sm, 1);
		width: nth($spec-panel-size--sm, 2); } }

// button enable special version
.sv-btn-on {

	position: fixed;
	left: 10px;
	top: 10px;
	padding-left: 1.9em;
	font-family: inherit;
	//font-size: inherit
	//line-height: inherit
	font-size: 18px;
	line-height: 30px;
	letter-spacing: normal;
	color: inherit;
	font-weight: bold;
	font-style: normal;
	box-sizing: border-box;
	z-index: 9999;


	//span
	//ont-size: 0
	//ine-height: 0

	svg {
		@include size(1.4em);
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0, -50%);
		box-sizing: border-box;

		&.icon-spec-on {
			display: block;

			.vspec & {
				display: none; } }

		&.icon-spec-off {
			display: none;

			.vspec & {
				display: block; } } }

	path {
		fill: currentColor; }

	// in header
	.header & {
		position: absolute;
		left: auto;
		right: 15px;
		top: 15px;

		@include media('<tab-lg') {
			right: 80px;
			top: auto;
			bottom: 25px; }

		.home-page & {
			color: white;

			@include media('<tab-lg') {
				color: inherit; } }

		.#{$spec-mod} & {
			color: currentColor;
			top: auto;
			right: 0;
			bottom: 60px;

			@include media('<tab-lg') {
				top: auto;
				bottom: 10px;
				right: 0; }

			@include media('<mob-lg') {
				order: 4;
				position: relative;
				top: auto;
				bottom: auto;
				right: auto; } } }

	// in footer
	.footer & {
		position: absolute;
		left: auto;
		top: auto;
		bottom: 20px;
		//position: absolute
		//left: auto
		//right: 15px
		//top: auto
		//bottom: 10px
		color: rgba(white, 0.8);

		.#{$spec-mod} & {
			color: currentColor;
			position: relative;
			bottom: auto; } } }

.spec-panel .sv-btn {
	$size: 42px;
	$arr: 24px;

	width: $size;
	height: $size;
	position: relative;
	font-size: 16px;
	line-height: 1;
	letter-spacing: normal;
	text-decoration: none;
	border-radius: 1px;
	//border: 3px solid
	//border-color: rgba($spec-text-color, 0.2) rgba($spec-text-color, 0.6) $spec-text-color rgba($spec-text-color, 0.4)
	border: 1px solid $spec-text-color;
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0,0,0,0) !important;
	user-select: none !important;

	@include media('<mob-md') {
		$size: 36px;
		width: $size;
		height: $size; }

	.#{$spec-mod--schemecolor-wb} & {
		border-color: $spec-text-color--scheme-wb; }

	.#{$spec-mod--schemecolor-blue} & {
		border-color: $spec-text-color--scheme-blue; }

	&::after {
		content: "";
		display: none;
		position: absolute;
		top: 50%;
		right: -22px;
		height: $arr;
		width: $arr;
		margin-top: -#{round($arr/2)};
		@include triangle("left", currentColor, round($arr/2), 1.2);

		@include media('<mob-md') {
			border: none;
			@include triangle("top", currentColor, round($arr/2), 1.2);
			top: auto;
			right: auto;
			left: 50%;
			bottom: -20px;
			margin: 0 0 0 -#{round($arr/2)}; } } }

