.spec-panel {
	display: none;
	position: fixed;
	left: 0;
	top: 0;
	height: 100%;
	z-index: 999;
	font-size: 16px;
	line-height: 1;
	overflow: hidden;
	box-shadow: 0 0 0 1px currentColor;
	background-color: inherit;
	will-change: transform;
	transition-property: transform;
	transition-duration: 0.2s;
	transition-timing-function: ease-in-out;
}

@media (max-width: 479px) {
	.spec-panel {
		height: 70px;
		width: 100%;
		border-width: 0 0 2px 0;
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(0, 0, 0, 0.3);
	}
	.vspec--schemecolor-white .spec-panel {
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(255, 255, 255, 0.3);
	}
	.vspec--schemecolor-blue .spec-panel {
		box-shadow: 0 0 0 2px currentColor, 0 4px 4px rgba(0, 76, 146, 0.3);
	}
}

.vspec .spec-panel {
