	left: 10px;
	top: 10px;
	padding-left: 1.9em;
	font-family: inherit;
	font-size: 18px;
	line-height: 30px;
	letter-spacing: normal;
	color: inherit;
	font-weight: bold;
	font-style: normal;
	box-sizing: border-box;
	z-index: 9999;
}

.sv-btn-on svg {
	width: 1.4em;
	height: 1.4em;
	position: absolute;
	left: 0;
	top: 50%;
	transform: translate(0, -50%);
	box-sizing: border-box;
}

