.spec-panel .sv-btn {
	width: 42px;
	height: 42px;
	position: relative;
	font-size: 16px;
	line-height: 1;
	letter-spacing: normal;
	text-decoration: none;
	border-radius: 1px;
	border: 1px solid #000;
	box-sizing: border-box;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0) !important;
	user-select: none !important;
}

@media (max-width: 479px) {
	.spec-panel .sv-btn {
		width: 36px;
		height: 36px;
	}
}

.vspec--schemecolor-white .spec-panel .sv-btn {
	border-color: #fff;
}

.vspec--schemecolor-blue .spec-panel .sv-btn {
	border-color: #004c92;
}

.spec-panel .sv-btn::after {
	content: "";
	display: none;
	position: absolute;
	top: 50%;
	right: -22px;
	height: 24px;
	width: 24px;
	margin-top: -12px;
	width: 0;
	height: 0;
	z-index: 2;
	border-right: 14px solid currentColor;
	border-bottom: 12px solid transparent;
	border-top: 12px solid transparent;
}

@media (max-width: 479px) {
	.spec-panel .sv-btn::after {
		border: none;
		width: 0;
		height: 0;
		z-index: 2;
		border-bottom: 14px solid currentColor;
		border-left: 12px solid transparent;
		border-right: 12px solid transparent;
		top: auto;
		right: auto;
		left: 50%;
		bottom: -20px;
		margin: 0 0 0 -12px;
	}
}

