.spec-panel .sv-btn[data-toggle]:hover, .vspec--schemecolor-blue
.spec-panel .sv-btn[data-toggle]:focus, .vspec--schemecolor-blue
.spec-panel .sv-btn.active[data-toggle]:hover, .vspec--schemecolor-blue
.spec-panel .sv-btn.active[data-toggle]:focus {
