.spec-panel .sv-btn.active, .spec-panel .sv-btn.active:hover, .spec-panel .sv-btn.active:focus {
	cursor: default;
	box-shadow: none;
	border-width: 4px;
}

.spec-panel .sv-btn.active::after {
