}

.spec-panel .sv-btn-off {
	display: none;
}

.vspec .spec-panel .sv-btn-off {
	display: block;
}

.vspec .spec-panel .sv-btn-off::before {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	top: 50%;
	width: 40px;
	height: 3px;
